<template>
  <div class="container font-neodgm mt-3">
    <div class="d-flex flex-column align-items-center signupbox">
      <img src="@/assets/signup.png" alt="signup" style="width:200px;" class="ms-4">
      <h4 class="my-2 ms-5">   !</h4>
      <el-form :model="SignupForm" :rules="rules" label-width="120px" id="formstyle" ref="SignupForm" class="mt-1">
          <el-form-item label="" prop="userid">
          <div class="d-flex">
            <el-input v-model="SignupForm.userid" autocomplete="off"></el-input>
            <el-button class="ms-4" type="warning" @click="onIdCheck"> </el-button>
          </div>
          </el-form-item>
        <el-form-item label="" prop="username">
          <el-input v-model="SignupForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="SignupForm.password" autocomplete="off" type="password"></el-input>
        </el-form-item>
        <el-form-item label="" prop="passwordconfirmation">
          <el-input v-model="SignupForm.passwordconfirmation" autocomplete="off" type="password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="onSignup('SignupForm')" style="background-color:#8EBF24; color:white;" class="me-5"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-dialog
      title="  " width="30%" :visible.sync="modalVisible">
      <span>{{ msg }}</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="modalVisible = false" type="warning"></el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: "Home",
  data(){
    var checkpassword = (rule,value,callback) => {
      if (!value) {
        return callback(new Error(' '))
      }
      if (value !== this.SignupForm.password){
        return callback(new Error(' .'))
      }
      else{
        callback()
      }
    }
    return {
      SignupForm:{
        userid:undefined,
        password:null,
        username:null,
        passwordconfirmation:null,
        idCheck:false,
      },
      modalVisible:false,
      msg: undefined,
      rules: {
        userid:[
          { required:true, message:" !", trigger:"blur"},
        ],
        password:[
          { required:true, message:" ", trigger:"blur"},
          { pattern: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&^])[A-Za-z\d$@$!%*#?&^]{9,16}$/, message:' , ,   .', trigger:'blur' },
        ],
        username:[
          { required:true, message:" ", trigger:"blur"}
        ],
        passwordconfirmation:[
          { validator: checkpassword, trigger:'blur'},
          { required:true, message:"  ", trigger:"blur"}
        ],
      },
    }
  },
  methods:{
    onSignup(formName){
      this.$refs[formName].validate((valid) => {
        if (valid && this.idCheck) {
          this.$store.dispatch('requestSignup',{'password':this.SignupForm.password,'userid':this.SignupForm.userid,'username':this.SignupForm.username})
          .then(res=>{
            console.log(res,'')
            this.$router.push({name:'Home'})
          })
          .catch(err=>{
            console.log(err)
          })
        }
        else{
          alert('  !')
        }
      })
    },
    onIdCheck(){
      if (this.SignupForm.userid !== undefined){
        this.$store.dispatch('IdCheck',{'userId':this.SignupForm.userid})
        .then(res=>{
          console.log(res)
          this.idCheck = true
          this.modalVisible = true
          this.msg = "   ."
        })
        .catch(err=>{
          console.log(err)
          this.modalVisible = true
          this.msg = "   "
        })
      }
    },
  }
}
</script>
<style>
.signupbox{
  background-color: white;
}
</style>

